import { InjectionToken } from '@angular/core';
import {
  DeepPartial,
  IdentityJourneyConfiguration,
  JourneyCommonPropertyTypes,
  JourneyConfigKeyValuePair,

  /**
   * Unused JourneyConfigValueType symbol needed for known ngPackagr issue (present in built library) related to using inferred types:
   * https://github.com/ng-packagr/ng-packagr/issues/1405
   */
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  JourneyConfigValueType,
} from '@backbase/identity-common-ang';
import { LocalizationOption } from './util';

export type UserIdentitySecurityCenterJourneyConfiguration = JourneyConfigKeyValuePair &
  Pick<JourneyCommonPropertyTypes, 'notificationDismissTime'>;

export type UserManageProfileJourneyConfiguration = JourneyConfigKeyValuePair &
  Pick<
    JourneyCommonPropertyTypes,
    | 'notificationDismissTime'
    | 'emailAddressTypes'
    | 'postalAddressTypes'
    | 'phoneNumberTypes'
    | 'maxEmailAddresses'
    | 'maxPhoneNumbers'
    | 'maxPostalAddresses'
    | 'showWidgetHeader'
    | 'phoneNumberCountryCode'
    | 'phoneNumberDebounceTime'
    | 'enablePhoneNumberFormatter'
  >;

export type DeviceInformationJourneyConfiguration = JourneyConfigKeyValuePair &
  Pick<JourneyCommonPropertyTypes, 'notificationDismissTime' | 'routeParamNameUserId'>;

export type UserLocalizationJourneyConfiguration = JourneyConfigKeyValuePair &
  Pick<JourneyCommonPropertyTypes, 'notificationDismissTime' | 'showEditableLanguage'> & {
    localizationOptions: Array<LocalizationOption>;
  };

export interface IdentitySelfServiceJourneyConfiguration extends IdentityJourneyConfiguration {
  userIdentitySecurityCenter: UserIdentitySecurityCenterJourneyConfiguration;
  userManageProfile: UserManageProfileJourneyConfiguration;
  deviceInformation: DeviceInformationJourneyConfiguration;
  userLocalization: UserLocalizationJourneyConfiguration;
}

export const IdentitySelfServiceJourneyConfigurationToken = new InjectionToken<
  DeepPartial<IdentitySelfServiceJourneyConfiguration>
>('IdentitySelfServiceJourneyConfiguration injection token');
